import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadDogs();
    }

	loadDogs() {
		const { dogStore } = this.props.stores;
		dogStore.load();
	}

	render() {
		const { dogStore } = this.props.stores;
		const { dogs } = dogStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<div class='col-12 col-sm-6 col-lg-2 mt-2 d-flex'>
									{/* {JSON.stringify(dog, null, 4)} */}
									<div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
										{!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3'>
										<span class='float-right'>
											{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)} Ã¥r</span>}
											{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
										</span>
										<a class='stretched-link' href={`/dogs/${dog.id}`}>
											<h5>{dog.shortname || dog.name}</h5>
										</a>
										<small>
											{dog.chipdId && <small>{dog.chipId}</small>}
										</small>
									</div>
								</div>
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/dogs/new'
							>
								<i class='fas fa-plus' /> Legg til hunder
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Dogs;
